#nodemap_wrapper{
    width: 100%;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #ccc;
    background: white;
    position: fixed;
}
#nodemap_wrapper h1{
    font-size: 20px;
}
#nodemap{
    display: flex;
    align-self: center;
}
#nodemap h1{
    font-size: 16px;
    margin-left: 10px;
    padding: 3px;
    border-radius: 3px;
}
#nodemap h1:nth-child(1){
    background: gold;
}
.node_down{
    background: red;
}
.node_down:hover{
    cursor: pointer;
}

#add_note{
    position: absolute;
    top: 0;
    bottom: 0;
    margin-top: auto;
    margin-bottom: auto;
    right: 5px;
    height: 20px;
}